body {
  background-color: #202124;
}

.app__Search {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #202124; /* or any other dark color */
}

h1 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 20px;
}

.titleBooks{
  color:#dcddde;
}

.app__search-container {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.searchbar {
  position: relative;
  padding: 10px;
  font-size: 16px;
  width: 100%;
  border: none;
  border-radius: 20px;
  background-color: #f1f3f4;
  outline: none;
}

.searchbar:hover,
.searchbar:focus {
  background-color: #fff;
}

.searchbar::placeholder {
  color: #999;
}


.app__book-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
}

.book-item {
  background-color: #f1f1f1;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.book-cover {
  width: 150px;
  height: auto;
  margin-bottom: 10px;
}

.book-title {
  font-size: 16px;
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;
}

.book-title:hover {
  color: black;
  font-weight: bold;
}

.clear-input {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #9194a3;
  cursor: pointer;
}

.clear-input:hover {
  color: #000;
}

@media screen and (max-width: 450px) {
  .app__search-container {
    flex-direction: column;
    align-items: stretch;
  }

  .searchbar {
    margin-bottom: 10px;
    width: 95%;
  }
  .app__book-list{
    grid-template-columns: 1fr;
  }

  .book-item {
    padding: 5px;
  }

  .book-cover {
    width: 100px;
  }

  .book-title {
    font-size: 14px;
  }
}
